.intro {
  min-height: 65vh;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.socialLinks>* {
  margin: 0 8px
}

.name {
  font-weight: 200;
  margin: 0 0 2rem 0;
}

.description {
  font-style: italic;
  margin: 0 0 2rem 0;
  width: 400px;
  max-width: 80vw;
  text-align: center;
}

.bryceCanyon {
  transform: translateX(0);
}

@media screen and (min-width: 0px) and (max-width: 800px) {
  .bryceCanyon{
    transform: translateX(20%);
  }
}

@media screen and (min-width: 800px) and (max-width: 1200px) {
  .bryceCanyon{
    transform: translateX(10%);
  }
}

